import React from 'react';
import './App.css';
import Footer from './components/Footer.js';
import Home from './screens/Home.js';
import About from './screens/About.js';
import Projects from './screens/Projects.js';
import Art from './screens/Art.js';
import ExpandedCard from './components/ExpandedCard.js';
import projects from './components/ProjectFiles.js';
import art from './components/ArtFiles.js';

import {
  HashRouter as Router,
    Switch,
    Route,
    NavLink
} from "react-router-dom";

function App() {
  return (
    <div className="App">
      <div className="colorbar" />
        <Router basename={process.env.PUBLIC_URL}>
            <div>
              <div className="navbar-main">
                <h2>tylerharrison</h2>
                <NavLink activeClassName="active-navitem underline underline-blue" className="navitem underline" exact to="/">home</NavLink>
                <NavLink activeClassName="active-navitem underline underline-purple" className="navitem underline" to="/about">about</NavLink>
                {/* <NavLink activeClassName="active-navitem underline underline-orange" className="navitem underline" to="/projects">projects</NavLink> */}
                {/* <NavLink activeClassName="active-navitem underline underline-yellow" className="navitem underline" to="/art">art</NavLink> */}
              </div>
              <Switch>
                {projects.map((project) => (
                  <Route exact path={"/projects/" + project.name} key={project.name} component={() => <ExpandedCard project={project} />} />
                ))}
                <Route exact path={"/"} component={Home} />
                <Route exact path={"/about"} component={About} />
                <Route exact path={"/projects"} component={() => <Projects projects={projects} />} />
                <Route exact path={"/art"} component={() => <Art art={art} activeImageIndex={0} />} />
              </Switch>
            </div>
        </Router>
        <Footer />
    </div>

  );
}

export default App;
