import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub, faLinkedin, faItchIo } from '@fortawesome/free-brands-svg-icons';
import { faFileAlt } from '@fortawesome/free-solid-svg-icons';
import resume from '../assets/resume.pdf';
import styles from '../styles/Home.module.css';

export default class Home extends React.Component {
    render() {
      return (
        <div className="fadediv flex-container wrap-content">
          <div className={styles.content}>
            <div className={styles.title}>
              <h2 className={styles.subtitle}>
                Hi! I'm
              </h2>
              <h1>Tyler Harrison,</h1>
              <h1 className={styles.titleListContainer}>
                <ul className={styles.titleList}>
                 <li>an undergrad.</li>
                 <li>a programmer.</li>
                 <li>a researcher.</li>
                 <li>a space enthusiast.</li>
               </ul>
              </h1>
            </div>
            <div className={styles.text}>
              <p>Programming isn't just a job for me, it's been my life for as long as I can remember. I'm dedicated to improving our world's infrastructure one code-sprint at a time!</p>
              <p>Lets talk! Reach out anytime at <a href="mailto:tyleraharrison@gmail.com">tyleraharrison@gmail.com</a>.</p>
              <a title="code" href="https://github.com/tyleraharrison" target="_blank" rel="noopener noreferrer" className={styles.socialItem}><FontAwesomeIcon icon={faGithub} /></a>
              <a title="linkedin" href="https://www.linkedin.com/in/tyler-harrison-895486ab/" rel="noopener noreferrer" target="_blank" className={styles.socialItem}><FontAwesomeIcon icon={faLinkedin} /></a>
              <a title="resume" href={resume} target="_blank" rel="noopener noreferrer" className={styles.socialItem}><FontAwesomeIcon icon={faFileAlt} /></a>
            </div>
          </div>
        </div>
      )
    }
}
