import React from 'react';
import styles from '../styles/Art.module.css';

export default class Art extends React.Component {
  render() {
      const art = this.props.art;
      return (
        <div className="fadediv flex-container content">
          <div className={styles.content}>
            <h2>Art Portfolio</h2>
            <p>A collection of digital illustrations from my 2019-2020 concentration. Themed around the word:<br /><b>inÂ·troÂ·specÂ·tion</b>. the examination or observation of one's own mental and emotional processes.</p>
            <p><b>Tools:</b> Paint Tool SAI, Clip Studio Paint, Photoshop</p>
            <div className={styles.grid}>
              {art.map((image, index) => (
                <div className="imageWrapper" key={index}>
                  <img title={image.title} className="image" src={image.img} alt={image.alt}/>
                </div>
              ))}
            </div>
          </div>
        </div>
      )
  }
}
