import React from 'react';
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChevronLeft } from '@fortawesome/free-solid-svg-icons';
import ReactHtmlParser from 'react-html-parser';
import styles from '../styles/Projects.module.css';

export default class ExpandedCard extends React.Component {
  render() {
    const project = this.props.project;
    return (
      <div className="flex-container">
        <div className={styles.cardContent}>
          <Link className={styles.backBtn + " hvr-backward"} to="/projects"><FontAwesomeIcon icon={faChevronLeft} /></Link>
          <div>
            <h1 className="underline underline-pastelpink">{project.title}</h1>
            <div className="inline">
              <p className={styles.link}>{ReactHtmlParser(project.link)} </p>
              <p>/ {this.props.project.date}</p>
            </div>
            <div>
              <img className={styles.cardImg} src={project.img} />
              <img className={styles.cardImg} src={project.alt} />
            </div>
            <p>{project.description}</p>
            <p><b>Tools:</b> {project.technologies}</p>
          </div>
        </div>
      </div>
    );
  }
}
