import React from 'react';
import { Link } from "react-router-dom";
import styles from '../styles/Projects.module.css';

export default class Projects extends React.Component {
  render() {
    const projects = this.props.projects;
    return (
      <div className="fadediv flex-container">
        <div className={styles.content}>
          <h2>Projects Portfolio</h2>
          <p>A small selection of things I've conceptualized, designed, and developed, ranging from hackathons to educational games to wellness projects. Done both solo and collaboratively. </p>
          <div className={styles.grid}>
            {projects.map((project, index) => (
              <div key={index} className="hvr-grow imageWrapper">
                <Link to={"/projects/" + project.name}>
                  <img title={project.title + " / " + project.technologies} className="image" src={project.img} alt={project.name}/>
                </Link>
              </div>
            ))}
          </div>
        </div>
      </div>
    )
  }
}
